@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);
.auth_page {
  width: 100%;
  padding: 2.5rem 0;
  background: #fdfdfd;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.auth_page .auth_box {
  background: #f8f9fa;
  max-width: 400px;
  width: 100%;
  padding: 2.7rem 1.7rem;
  border-radius: 10px;
  border: solid 3px rgb(176, 176, 176);
}

.auth_page .pass {
  position: relative;
}

.auth_page .pass small {
  position: absolute;
  top: 50%;
  right: 5px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  opacity: 0.5;
}

.auth_page input::-webkit-input-placeholder {
  opacity: 0.5;
  font-size: 80%;
}

.auth_page input:-ms-input-placeholder {
  opacity: 0.5;
  font-size: 80%;
}

.auth_page input::placeholder {
  opacity: 0.5;
  font-size: 80%;
}

.auth_page a {
  text-decoration: none;
}

.auth_page a:hover {
  text-decoration: underline;
}
.loading{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.loading svg{
  font-size: 5px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  -webkit-animation: text 1s ease-in-out infinite;
          animation: text 1s ease-in-out infinite;
}
@-webkit-keyframes text{
  50% { opacity: 0.1 }
}
@keyframes text{
  50% { opacity: 0.1 }
}

.loading polygon{
  stroke-dasharray: 22;
  stroke-dashoffset: 1;
  animation: dash 4s cubic-bezier(0.445, 0.05, 0.55, 0.95)
  infinite alternate-reverse;
}
@-webkit-keyframes dash{
  to { stroke-dashoffset: 234 }
}
@keyframes dash{
  to { stroke-dashoffset: 234 }
}
.errMsg{
  background: rgb(214, 10, 10);
  color: #fff9;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 1.3px;
}

.successMsg{
  background: rgb(9, 158, 54);
  color: #fff9;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 1.3px;
}
.profile_info{
  background: #fff;
  width: 100%;    
  border: 2px solid rgb(112, 112, 112);
  border-radius: 0.3em;
  padding: 2.5rem 1.7rem;
}

.profile_info .pass{
  position: relative;
}

.profile_info .pass small{
  position: absolute;
  top: 50%;
  right: 5px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  opacity: 0.5;
}

.profile_info .info_avatar{
  width: 180px;
  height: 180px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
  margin: 0 auto;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  cursor: pointer;
}
.profile_info .info_avatar img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.profile_info .info_avatar span{
  position: absolute;
  bottom: -100%;
  left: 0;
  width: 100%;
  height: 50%;
  text-align: center;
  color: crimson;
  transition: 0.3s ease-in-out;
  background: #fff5;
}
.profile_info .info_avatar:hover span{
  bottom: -15%;
}

.profile_info .info_avatar #file_up{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
}
::-webkit-file-upload-button{
  cursor: pointer;
}
.category{
  max-width: 500px;
  margin: 2em auto;
}

.category form{
  width: 100%;
  margin-bottom: 2rem;
}
.category form label{
  display: block;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.category form input,
.category form button{
  height: 35px;
  border: none;
  outline: none;
  border-bottom: 1px solid #555;
}

.category form input{
  -webkit-flex: 1 1;
          flex: 1 1;
}

.category form button{
  width: 80px;
  background: #555;
  color: white;
  margin-left: 10px;
}

.category .category_row{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  cursor: pointer;
}
.home_page {
  margin: 1rem 0;
}

.home_page h3 a {
  text-transform: uppercase;
  text-decoration: none;
  color: black;
  font-size: italic;
  letter-spacing: 3;
  margin-top: 1rem;
  cursor: pointer;
}

.home_page h3:hover a {
  color: rgb(168, 105, 117);
}

.home_page h3 a small{
  font-size: 15px;
}

.home_blogs{
  display: grid;
  grid-gap: 10px;
  margin-bottom: 15px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  grid-gap: 10px;
}

.outside_sidebar{
  margin-bottom: -2em;
}

.headera:hover{
  color: black;
  background-color:white;
  border-radius: 0.2em;
}

.headera:focus {
    color: black;
    background-color:white;
    border-radius: 0.2em;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
small {
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  font-weight:600;
  color: black
}

.headera{
  color: white
}
.blogs_category {
  width: 100%;
  margin: 1rem 0;
}

.blogs_category .show_blogs{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  grid-gap: 10px;
  margin-bottom: 15px;
}


.avatar_comment{
  width: 70px;
  min-width: 70px;
  text-align: center;
  padding: 5px;
}

.avatar_comment img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.avatar_comment small a{
  color: #444;
  text-decoration: none;
  font-weight: 500;
}

.avatar_comment small a:hover{
  color: crimson;
}

.comment_box {
  width: 100%;
  border: 1px solid #ddd;
  margin-bottom: 0.75rem;
}

.avatar_reply{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 0.25rem;
}

.avatar_reply img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}
.avatar_reply .reply-text{
  display: block;
  opacity: 0.5;
  font-size: 11px;
}

.comment_box .comment_nav {
  cursor: pointer;
  display: none;
}

.comment_box:hover .comment_nav {
  display: block;
}
.comment_box .comment_nav i:hover {
  color: crimson;
}
